form {
  width: 27rem;
  min-height: 19rem;
  height: max-content;
  padding: 1rem;
  margin: 0rem;
  display: grid;
  grid-template-areas:
    "imagesContainer imagesContainer"
    "title title"
    "usernameLabel usernameInput"
    "passwordLabel passwordInput"
    "serverLabel serverInput"
    "databaseLabel databaseInput"
    "loginButton loginButton"
    "forgotButton signupButton";
  gap: 1rem;
  grid-template-columns: max-content 1fr;
  grid-template-rows: repeat(6, max-content);
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 3px;
  border: none;
  position: relative;

  > div.imagesContainer {
    grid-area: imagesContainer;
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: max-content;
    > img {
      width: 10rem;
      max-width: 50%;
      height: 100%;
      object-fit: scale-down;
      object-position: center;
    }
  }
  > h1 {
    grid-area: title;
    width: 100%;
    height: max-content;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: left;
  }
  > label {
    color: black;
    font-size: 0.9rem;
    font-weight: lighter;
    &[for="username"] {
      grid-area: usernameLabel;
    }
    &[for="password"] {
      grid-area: passwordLabel;
    }
    &[for="server"] {
      grid-area: serverLabel;
    }
    &[for="database"] {
      grid-area: databaseLabel;
    }
  }
  > select {
    color: black;
    background-color: white;
    padding: 0.2rem;
    border-radius: 3px;
    border: 1px solid grey;
    font-size: 1rem;
    font-weight: normal;
    &#server {
      grid-area: serverInput;
    }
    &#database {
      grid-area: databaseInput;
    }
  }
  > input {
    color: black;
    background-color: white;
    padding: 0.2rem;
    border-radius: 3px;
    border: 1px solid grey;
    font-size: 1rem;
    font-weight: normal;
    &#username {
      grid-area: usernameInput;
    }
    &#password {
      grid-area: passwordInput;
    }
  }
  > button {
    width: 100%;
    height: max-content;
    font-size: 1.2rem;
    font-weight: normal;
    background-color: #3d3fb6;
    color: white;
    border: none;
    border-radius: 3px;
    pointer-events: all;
    cursor: pointer;
    padding: 0.56rem;
    &:hover {
      background-color: #393cd4;
    }
    &.login {
      grid-area: loginButton;
    }
    &.secondary {
      background-color: transparent;
      width: max-content;
      height: max-content;
      color: grey;
      font-size: 0.9rem;
      font-weight: lighter;
      justify-self: end;
      text-decoration: underline;
      &:hover {
        color: rgb(66, 66, 66);
      }
    }
  }
}
